.sidebar {
  width: 200px;
  background-color: #f8f8f8;
  border-right: 1px solid #e7e7e7;
  padding: 20px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
}

.sidebar-title {
  margin-top: 0;
  margin-bottom: 20px;
  font-size: 1.5em;
  color: #555555;
}

.sidebar-list-container {
  flex: 1;
  display: flex;
  align-items: center; 
  justify-content: center;
}

.sidebar-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.sidebar-item {
  padding: 10px 0;
  cursor: pointer;
  color: #555555;
  transition: color 0.3s, opacity 0.3s, font-size 0.3s;
  opacity: 0.7;
  font-size: 1em;
  text-align: center; /* Centers text horizontally within each item */
}

.sidebar-item:hover {
  color: #000000;
}

.sidebar-item.active {
  font-size: 1.2em;
  opacity: 1;
  color: blue;
}